<template>
  <div class="teleregi">
    <header-component title="手机注册"></header-component>
    <router-view></router-view>
    <div class="am-top">
      <div class="am-form-box">
        <label class="zhanghao">手机号</label>
        <div class="am-account">
          <input type="number" id="phoneInput" class="txtSize"  placeholder="请输入您的手机号码" maxlength="11">
          <img @click="phoneCancel" class="am-account-cancel" src="../../assets/images/Login/Login_06.png"/>
        </div>
        <label class="zhanghao">验证码</label>
        <div class="code">
          <input id="codeInput" type="text" class="code-input"  placeholder="输入验证码" maxlength="6">
          <button v-show="show" @click="sendCode" class="send-code" id="sendCode" type="button">获取验证码</button>
          <span v-show="!show" class="count">({{count}})重新发送</span>
        </div>
        <label class="mima">密码</label>
        <div class="am-passWord">
          <input id="pwdInput" type="password" class="am-pwd" placeholder="填写密码" maxlength="16">
          <img @click="pwdCancel" id="pwdCancel" class="am-password-cancel" :src="login_07"/>
          <img @click="pwdShow" class="am-password-show" src="../../assets/images/Login/Login_06.png"/>
        </div>
        <p class="am-loginBtn">
          <button @click="register" type="button" class="am-btn-default">注&nbsp;册</button>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  import HeaderComponent from './header.vue'
  import login_05 from '@/assets/images/Login/Login_05.png'
  import login_07 from '@/assets/images/Login/Login_07.png'
  var urlhost = process.env.API_AJAX;
  export default {
    name:"ActivityPhone",
    data() {
      return {
        aa:true,
        login_05:login_05,
        login_07:login_07,
        show: true,
        count: 60,
        timer: null,
      }
    },
    mounted:function () {

      $('#phoneInput').on('input', function(){
        var number = $(this).val().replace(/\s/g, '');
        if(number.length == 11){

          if(/^1[3|4|5|7|8][\d]{9}$/.test(number)){

          }else{
            this.$layer.msg("输入的手机号码有误");
            $('#phoneInput').val("");
          }
        }
      });
    },
    methods:{
      phoneCancel:function () {
        $('#phoneInput').val("");
      },
      pwdCancel:function () {
        if(this.aa == true){
          $("#pwdInput").attr("type","text");
          $("#pwdCancel").attr("src",this.login_05);
          this.aa=false;
        }else{
          $("#pwdInput").attr("type","password");
          $("#pwdCancel").attr("src",this.login_07);
          this.aa=true;
        }
      },
      pwdShow:function () {
        $('#pwdInput').val("");
      },
      sendCode:function () {
        var _this = this;
        var phone = $("#phoneInput").val();
        if(phone === ''){
          _this.$layer.msg("手机号不能为空")
        }else {
          $.ajax({
            type: 'POST',
            url: urlhost+"register/code",
            data$Type: "jsonp",
            data: {
              'phone': phone
            },
            success: function (response) {
              var mess = JSON.parse(response);
              console.log(mess)
              if (mess.status == 0) {

                _this.$layer.msg(mess.message)
              }else{
                _this.count = 60;
                _this.show = false;
                var timer = setInterval(function () {
                  if (_this.count == 0) {
                    _this.show = true;
                    clearInterval(timer);
                  } else {
                    _this.count--;
                  }
                }, 1000);
                _this.$layer.msg(mess.message)
              }
            },
          });

        }
      },
      register:function () {
        var _this = this;
        var phone = $('#phoneInput').val();
        var code = $('#codeInput').val();
        var password = $('#pwdInput').val();
        if(phone === '' || code === '' || password === ''){
          _this.$layer.msg("内容不能为空");
        }else{
          $.ajax({
            type: 'POST',
            url: urlhost+"register/postRegister?channel_id=51activity",
            data$Type: "jsonp",
            data: {
              'phone': phone,
              "password":password,
              "code":code
            },
            success: function (response) {
              var msg = JSON.parse(response);
              if(msg.status === 0){
                _this.$layer.msg(msg.message)
              }else if(msg.status === 1){
                _this.$alert("注册成功!").then(function () {
                  _this.$router.push({
                    path:"/visit/activityLogin"
                  })
                });
              }
            },
          });

        }
      }

    },
    components: {
      HeaderComponent,
    }
  }
</script>

<style lang="scss">
  @function n($n) {
    @return $n  / 64 + rem;
  };

  .teleregi{
    .header{
      position: fixed;
      top:0;
      left:0;
      width: 100%;
      height: n(90);
      display: inline-flex;
      align-items: center;
      border-bottom: 1px solid #d8d8d8;
      img{
        position: absolute;
        left: 0.2rem;
        width: n(70);
        height: n(50);
      }
      a{
        width: 100%;
        display: inline-block;
        text-align: center;
        font-size: n(34);
      }
    }
    .am-top{
      width: 90%;
      margin: auto;
      margin-top: n(120);
      .am-form-box{
        width: 100%;
        margin-bottom: 0;
        .am-account{
          width: 100%;
          height: n(105);
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid #f3f3f3;
          input{
            border: none;
            outline: none;
            width: 90%;
            height: n(70);
            font-size: n(28);
            color: #b0b0b0;
          }
          img{
            width: n(50);
            height: n(50);
          }
        }
        .zhanghao{
          margin-top: n(20);
          font-weight: bold;
          display: inline-block;
          font-size: n(24);
          color: #424242;
        }
        .mima{
          @extend .zhanghao
        }
        .code{
          width: 100%;
          height: n(105);
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid #f3f3f3;
          .code-input{
            border: none;
            outline: none;
            width: 60%;
            height: n(70);
            font-size: n(28);
            color: #b0b0b0;
          }
          .send-code{
            border: none;
            outline: none;
            background: none;
            width: 30%;
            text-align: right;
            display: block;
            border-left:1px solid #f3f3f3;
            color: green;
          }
        }
        .am-passWord{
          width: 100%;
          height: n(105);
          display: inline-flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid #f3f3f3;
          input{
            border: none;
            outline: none;
            width: 70%;
            height: n(70);
            font-size: n(28);
            color: #b0b0b0;
          }
          img{
            width: n(50);
            height: n(50);
          }
        }
        .am-loginBtn{
          width: 100%;
          margin: n(80) 0 0 0;
          button{
            width: 100%;
            height: n(80);
            background: #fee837;
            font-size: n(30);
            border: none;
          }
        }
      }
    }
  }


</style>



